import { BrowserRouter, Route, Routes, Navigate } from "react-router-dom";
import { Suspense, lazy } from "react";
import ScrollToTop from "react-scroll-to-top";
import RouteScrollToTop from "./helper/RouteScrollToTop";

/* ---------------- Loader ---------------- */
const Loader = () => (
  <div className="flex items-center justify-center h-screen">
    <div className="animate-spin rounded-full h-10 w-10 border-b-2 border-purple-600"></div>
  </div>
);

/* ---------------- Public Pages ---------------- */
const HomePage = lazy(() => import("./pages/HomePage"));
const LoginPage = lazy(() => import("./pages/LoginPage"));
const RegisterPage = lazy(() => import("./pages/RegisterPage"));
const ContactPage = lazy(() => import("./pages/ContactPage"));
const CentreRegisterPage = lazy(() => import("./pages/CentreRegisterPage"));
const PatientRegisterPage = lazy(() => import("./pages/PatientRegisterPage"));
const ForgotPassword = lazy(() => import("./pages/ForgotPassword"));
const ResetPassword = lazy(() => import("./pages/ResetPassword"));
const SetPassword = lazy(() => import("./pages/SetPassword"));

const AllCenterPage = lazy(() => import("./pages/AllCentersPage"));
const ServicesPage = lazy(() => import("./pages/ServicesPage"));
const HealthPackages = lazy(() => import("./pages/HealthPackages"));

const ProductDetailsPage = lazy(() => import("./pages/ProductDetailsPage"));
const PackageDetsilsPage = lazy(() => import("./pages/PackageDetsilsPage"));
const ProfilePage = lazy(() => import("./pages/ProfilePage"));

const CartPage = lazy(() => import("./pages/CartPage"));
const CartPersonalPage = lazy(() => import("./pages/CartPersonalPage"));
const CartThankYouPage = lazy(() => import("./pages/CartThankYouPage"));

const PharmacyPage = lazy(() => import("./pages/PharmacyPage"));
const MedicalInstrumentsPage = lazy(
  () => import("./pages/MedicalInstrumentsPage"),
);
const HealthProductPage = lazy(() => import("./pages/HealthProductPage"));

const TermsBooking = lazy(() => import("./components/TermsBooking"));
const PrivacyBooking = lazy(() => import("./components/PrivacyBooking"));
const PaymentPolicyCentre = lazy(
  () => import("./components/PaymentPolicyCentre"),
);
const ReviewLogin = lazy(() => import("./components/ReviewLogin"));
const UserLogin = lazy(() => import("./components/UserLogin"));
const LoginCentre = lazy(() => import("./components/LoginCentre"));
const LoginAdmin = lazy(() => import("./components/LoginAdmin"));

/* ---------------- Centre Dashboard ---------------- */
const DashboardPage = lazy(() => import("./pages/DashboardPage"));
const DashboardProfilePage = lazy(() => import("./pages/DashboardProfilePage"));
const DoctorsPage = lazy(() => import("./pages/DoctorsPage"));
const EditDoctorPage = lazy(() => import("./pages/EditDoctorPage"));
const PackagePage = lazy(() => import("./pages/PackagePage"));
const FollowerPage = lazy(() => import("./pages/FollowerPage"));
const FollowingPage = lazy(() => import("./pages/FollowingPage"));
const SettingPage = lazy(() => import("./pages/SettingPage"));
const StatementPage = lazy(() => import("./pages/StatementPage"));
const EarningPage = lazy(() => import("./pages/EarningPage"));
const RefundPage = lazy(() => import("./pages/RefundPage"));
const CreateDoctors = lazy(() => import("./pages/CreateDoctors"));
const CreateService = lazy(() => import("./pages/CreateService"));
const CreatePackage = lazy(() => import("./pages/CreatePackage"));
const EditService = lazy(() => import("./pages/EditService"));
const EditPackage = lazy(() => import("./pages/EditPackage"));
const HelpSupportCentrePage = lazy(
  () => import("./pages/AdminDashboard/Centers/HelpSupportCentrePage"),
);
const UploadReportPage = lazy(
  () => import("./pages/AdminDashboard/Centers/UploadReportPage"),
);
const UserMedicalhistory = lazy(
  () => import("./pages/AdminDashboard/Centers/UserMedicalhistory"),
);

/* ---------------- User Dashboard ---------------- */
const UserProfilePage = lazy(
  () => import("./pages/UserDashboard/UserProfilePage"),
);
const MedicalHistoryPage = lazy(
  () => import("./pages/UserDashboard/MedicalHistoryPage"),
);
const ReportsPage = lazy(
  () => import("./pages/AdminDashboard/Users/ReportsPage"),
);
const AddInsurancePage = lazy(
  () => import("./pages/UserDashboard/AddInsurancePage"),
);
const AppointmentsPage = lazy(
  () => import("./pages/UserDashboard/AppointmentsPage"),
);
const HelpSupportUserPage = lazy(
  () => import("./pages/UserDashboard/HelpSupportUserPage"),
);

/* ---------------- Admin Dashboard ---------------- */
const AdminDashboardPage = lazy(
  () => import("./pages/AdminDashboard/AdminDashboardPage"),
);
const AdminEarning = lazy(() => import("./pages/AdminDashboard/AdminEarning"));
const AdminProfilePage = lazy(
  () => import("./pages/AdminDashboard/AdminProfilePage"),
);
const HelpAndSupport = lazy(
  () => import("./pages/AdminDashboard/HelpAndSupport"),
);
const RegisteredCentrePage = lazy(
  () => import("./pages/AdminDashboard/Centers/RegisteredCentrePage"),
);
const RegisteredCenters = lazy(
  () => import("./pages/AdminDashboard/Centers/RegisteredCenters"),
);
const ApprovedCenters = lazy(
  () => import("./pages/AdminDashboard/Centers/ApprovedCenters"),
);
const RegisteredServices = lazy(
  () => import("./pages/AdminDashboard/Services/RegisteredServices"),
);
const CategoryDetailsPage = lazy(
  () => import("./pages/AdminDashboard/Services/CategoryDetailsPage"),
);
const CentreServicesPage = lazy(
  () => import("./pages/AdminDashboard/Centers/CentreServicesPage"),
);
const RegisteredUsers = lazy(
  () => import("./pages/AdminDashboard/Users/RegisteredUsers"),
);
const BookingByCentrePage = lazy(
  () => import("./pages/AdminDashboard/Booking/BookingByCentrePage"),
);
const BookingAdmin = lazy(() => import("./pages/BookingAdmin"));
const BookingSummary = lazy(() => import("./pages/BookingSummary"));
const EarningSummaryByMonthPage = lazy(
  () => import("./pages/EarningSummaryByMonthPage"),
);
const ServicesByCentrePage = lazy(() => import("./pages/ServicesByCentrePage"));

/* ---------------- Protected Route ---------------- */
const ProtectedRoute = ({ children, requiredRole }) => {
  let token;
  let redirectPath = "/login";

  if (requiredRole === "user") {
    token = localStorage.getItem("userToken");
  } else if (requiredRole === "centre") {
    token = localStorage.getItem("centreToken");
    redirectPath = "/centre-login";
  } else if (requiredRole === "admin") {
    token = localStorage.getItem("adminToken");
    redirectPath = "/manage4ceps-admin";
  }

  if (!token) return <Navigate to={redirectPath} replace />;

  return children;
};

function App() {
  return (
    // <CartProvider>
    <BrowserRouter>
      <RouteScrollToTop />
      <ScrollToTop smooth color="#A847F0" />
      <Suspense fallback={<Loader />}>
        <Routes>
          <Route exact path="/" element={<HomePage />} />
          <Route exact path="/centers" element={<AllCenterPage />} />
          <Route exact path="/login" element={<LoginPage />} />
          <Route exact path="/centre-login" element={<LoginCentre />} />
          <Route exact path="/manage4ceps-admin" element={<LoginAdmin />} />
          <Route exact path="/set-password" element={<SetPassword />} />
          <Route exact path="/forgot-password" element={<ForgotPassword />} />
          <Route exact path="/reset-password" element={<ResetPassword />} />
          <Route exact path="/register" element={<RegisterPage />} />
          <Route exact path="/contact" element={<ContactPage />} />
          <Route
            exact
            path="/centre-register"
            element={<CentreRegisterPage />}
          />
          <Route
            exact
            path="/user-register"
            element={<PatientRegisterPage />}
          />

          <Route exact path="/earning" element={<EarningPage />} />
          <Route exact path="/all-service" element={<ServicesPage />} />

          <Route exact path="/all-package" element={<HealthPackages />} />

          <Route exact path="/user-login" element={<UserLogin />} />
          <Route exact path="/review-login" element={<ReviewLogin />} />

          <Route
            exact
            path="/service-details/:id"
            element={<ProductDetailsPage />}
          />
          <Route
            exact
            path="/package-details/:id"
            element={<PackageDetsilsPage />}
          />
          <Route exact path="/profile/:id" element={<ProfilePage />} />
          <Route exact path="/cart" element={<CartPage />} />
          <Route exact path="/cart-personal" element={<CartPersonalPage />} />
          <Route exact path="/cart-thank-you" element={<CartThankYouPage />} />
          <Route exact path="/terms-booking" element={<TermsBooking />} />
          <Route exact path="/privacy-booking" element={<PrivacyBooking />} />
          <Route
            exact
            path="/payment-policy"
            element={<PaymentPolicyCentre />}
          />
          <Route exact path="/pharmacy" element={<PharmacyPage />} />
          <Route
            exact
            path="/medical-instruments"
            element={<MedicalInstrumentsPage />}
          />
          <Route exact path="/health-product" element={<HealthProductPage />} />

          {/* <Route exact path="/invoice" element={<Invoice />} /> */}

          {/* Centre Dashboard - Protected */}
          <Route
            exact
            path="/centre-dashboard"
            element={
              <ProtectedRoute requiredRole="centre">
                <DashboardPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/dashboard-profile"
            element={
              <ProtectedRoute requiredRole="centre">
                <DashboardProfilePage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/doctors"
            element={
              <ProtectedRoute requiredRole="centre">
                <DoctorsPage />
              </ProtectedRoute>
            }
          />

          <Route
            exact
            path="/edit-doctor/:id"
            element={
              <ProtectedRoute requiredRole="centre">
                <EditDoctorPage />
              </ProtectedRoute>
            }
          />

          <Route
            exact
            path="/package"
            element={
              <ProtectedRoute requiredRole="centre">
                <PackagePage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/follower"
            element={
              <ProtectedRoute requiredRole="centre">
                <FollowerPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/following"
            element={
              <ProtectedRoute requiredRole="centre">
                <FollowingPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/setting"
            element={
              <ProtectedRoute requiredRole="centre">
                <SettingPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/statement"
            element={
              <ProtectedRoute requiredRole="centre">
                <StatementPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/earning"
            element={
              <ProtectedRoute requiredRole="centre">
                <EarningPage />
              </ProtectedRoute>
            }
          />
     
       
          <Route
            exact
            path="/refund"
            element={
              <ProtectedRoute requiredRole="centre">
                <RefundPage />
              </ProtectedRoute>
            }
          />
        
          <Route
            exact
            path="/create-doctor"
            element={
              <ProtectedRoute requiredRole="centre">
                <CreateDoctors />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/create-service"
            element={
              <ProtectedRoute requiredRole="centre">
                <CreateService />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/create-package"
            element={
              <ProtectedRoute requiredRole="centre">
                <CreatePackage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/edit-service/:id"
            element={
              <ProtectedRoute requiredRole="centre">
                <EditService />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/edit-package/:id"
            element={
              <ProtectedRoute requiredRole="centre">
                <EditPackage />
              </ProtectedRoute>
            }
          />

          {/* User Dashboard - Protected */}
          <Route
            exact
            path="/user-dashboard"
            element={
              <ProtectedRoute requiredRole="user">
                <UserProfilePage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/medical-history"
            element={
              <ProtectedRoute requiredRole="user">
                <MedicalHistoryPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/booking-report"
            element={
              <ProtectedRoute requiredRole="user">
                <ReportsPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/add-insurance"
            element={
              <ProtectedRoute requiredRole="user">
                <AddInsurancePage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/appointments"
            element={
              <ProtectedRoute requiredRole="user">
                <AppointmentsPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/user-helpAndSupport"
            element={
              <ProtectedRoute requiredRole="user">
                <HelpSupportUserPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/centre-helpAndSupport"
            element={
              <ProtectedRoute requiredRole="centre">
                <HelpSupportCentrePage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/user-medical-history/:id"
            element={
              <ProtectedRoute requiredRole="centre">
                <UserMedicalhistory />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/centre/upload-report"
            element={
              <ProtectedRoute requiredRole="centre">
                <UploadReportPage />
              </ProtectedRoute>
            }
          />

          {/* User Dashboard end */}

          {/* Admin Dashboard - Protected */}
          <Route
            exact
            path="/admin-dashboard"
            element={
              <ProtectedRoute requiredRole="admin">
                <AdminDashboardPage />
              </ProtectedRoute>
            }
          />

          <Route
            exact
            path="/adminDashboard-profile"
            element={
              <ProtectedRoute requiredRole="admin">
                <AdminProfilePage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/adminDashboard-earning"
            element={
              <ProtectedRoute requiredRole="admin">
                <AdminEarning />
              </ProtectedRoute>
            }
          />

          <Route
            exact
            path="/earningSummary"
            element={
              <ProtectedRoute requiredRole="admin">
                <BookingSummary />
              </ProtectedRoute>
            }
          />

          <Route
            exact
            path="/earningSumary/:id"
            element={
              <ProtectedRoute requiredRole="admin">
                <EarningSummaryByMonthPage />
              </ProtectedRoute>
            }
          />

          <Route
            exact
            path="/adminDashboard-helpAndSupport"
            element={
              <ProtectedRoute requiredRole="admin">
                <HelpAndSupport />
              </ProtectedRoute>
            }
          />

          <Route
            exact
            path="/registered"
            element={
              <ProtectedRoute requiredRole="admin">
                <RegisteredCentrePage />
              </ProtectedRoute>
            }
          />

          <Route
            exact
            path="/center-registered"
            element={
              <ProtectedRoute requiredRole="admin">
                <RegisteredCenters />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/center-approved"
            element={
              <ProtectedRoute requiredRole="admin">
                <ApprovedCenters />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/all-services-list"
            element={
              <ProtectedRoute requiredRole="admin">
                <RegisteredServices />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/adminBooking"
            element={
              <ProtectedRoute requiredRole="admin">
                <BookingByCentrePage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/adminBooking/:id"
            element={
              <ProtectedRoute requiredRole="admin">
                <BookingAdmin />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/category-services/:id"
            element={
              <ProtectedRoute requiredRole="admin">
                <CategoryDetailsPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/centre-services/:id"
            element={
              <ProtectedRoute requiredRole="admin">
                <CentreServicesPage />
              </ProtectedRoute>
            }
          />
          <Route
            exact
            path="/services/:id"
            element={<ServicesByCentrePage />}
          />
          <Route
            exact
            path="/users"
            element={
              <ProtectedRoute requiredRole="admin">
                <RegisteredUsers />
              </ProtectedRoute>
            }
          />
        </Routes>
      </Suspense>
    </BrowserRouter>
    // </CartProvider>
  );
}

export default App;
